.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
  box-sizing: border-box;
}

.value {
  width: 100%;
  max-width: 300px;
  padding: 10px 15px;
  font-size: 16px;
  outline: none;
  border-radius: 4px;
  border: 2px solid #03a9f4;
  color: #222;
}

.value:focus {
  border-color: #0ab999;
}

.convertedValue {
  min-height: 30px;
  font-size: 18px;
  color: #222;
  cursor: pointer;
}
